.introImage {
  /* background-image: url("./assets/img/mustika-pertiwi.png"); */
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.6),
    rgba(255,255,255, 0.6)
  ),url("./assets/img/mustika-pertiwi-2.png");
  background-size: 100vmax;
  background-position-x: center;
  background-attachment: fixed;
}

.divider {
  background: #ff6666;
  height: 5px;
}

.column {
  float: left;
  /* width: 50%; */
}

.column-2 {
  float: left;
  width: 33.33%;
}

.column-2 img {
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 10px;
}

.left {
  width: 40%;
}

.right {
  width: 60%;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
  margin: -10px;
}

.row-2:after {
  content: "";
  display: table;
  clear: both;
}

.introText {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.introText-1 {
  text-align: center;
  line-height: 50%;
}

.introText-1 h1 {
  font-weight: 800;
}

.introText-1 h3 {
  line-height: 120%;
  font-weight: 800;
}

.introText-1 h4 {
  margin-top: -5px;
}

.introText-1 h5 {
  margin-top: 40px;
  margin-bottom: -5px;
}

.introText-1 h4 {
  line-height: 120%;
  margin-top: 5px;
  font-weight: 600;
  margin-bottom: 50px;
}

.introText-2 {
  /* line-height: 80%; */
  text-align: center;
}

.introText-2 h2 {
  margin-bottom: -20px;
}

.introText-3 {
  margin-top: 30px;
  text-align: center;
  line-height: 30%;
  background-color: #ff6666;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 10px;
}

.eventText {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: justify;
  margin-left: 20px;
  margin-right: 20px;
}

.eventText-1 {
  text-align: justify;
}

.eventText-1 h4 {
  font-weight: 600;
}

.eventButton {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.eventButton img {
  background-color: #fff;
  padding: 10px;
  border-radius: 10px;
}

.button {
  background-color: #ff6666; /* Green */
  border: none;
  color: white;
  padding: 5px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 10px;
  margin: 15px 1px 0 1px;
}

.button:hover {
  background-color: white;
  color: #ff6666;
  border: 1px solid #ff6666;
}

ul li {
  font-weight: 600;
}

.inviteText {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: justify;
  margin-left: 20px;
  margin-right: 20px;
}

.inviteText-1 h4 {
  font-weight: 600;
  text-align: justify;
}

.endSection {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: justify;
  margin-left: 20px;
  margin-right: 20px;
}

.endSection-image {
  margin-top: 10px;
  justify-content: center;
  display: flex;
  background-color: #f4f4f4;
  border-radius: 15px;
}

.keynoteText {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: justify;
  margin-left: 20px;
  margin-right: 20px;
}

.keynoteSpeakers-1 {
  /* margin-top: 20px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.keynote-1 {
  margin-top: 20px;
  margin-bottom: 20px;
}

.keynote-1 img {
  border-radius: 20px;
}

.keynote-1 h3 {
  line-height: 0;
}

